@import url(https://fonts.googleapis.com/css?family=Lato&family=Roboto:tal@1&display=swap);



.layout {
  min-width: 1300px;
  min-height: 97vh;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch; }

.content {
  margin-top: 4vw;
  padding: 0 0.5vw 0 0.5vw;
  display: grid;
  grid-template-columns: 2.8fr 1.2fr;
  grid-column-gap: 0.5vw; }

.wrapper {
  flex-grow: 1;
  flex-shrink: 0; }

.header {
  min-height: 5vw; }
  .header hr {
    border: 1px solid #223046; }

.headerWrap {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr; }

.logo {
  display: flex;
  justify-content: center; }
  .logo a {
    text-decoration: none; }
  .logo h2 {
    font-family: "Lato", sans-serif;
    color: #223046;
    font-size: 1.6vw;
    cursor: pointer; }

.person {
  display: flex;
  justify-content: space-around;
  align-items: center; }
  .person i {
    color: #223046;
    font-size: 1.6vw;
    cursor: pointer; }

nav {
  text-align: center;
  min-height: 5vw; }
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
    min-height: 5vw;
    display: grid;
    grid-template-columns: repeat(4, 1fr); }
    nav ul li {
      display: inline-block;
      align-self: center;
      transition: all 0.2s ease-in; }
      nav ul li a {
        text-decoration: none;
        font-family: "Robote", sans-serif;
        font-size: 1vw;
        color: #223046; }
      nav ul li a:hover {
        color: black; }

.exchange {
  min-height: 16vw;
  border: 1px solid #bfc4cd;
  padding: 1vw 0 1vw 0; }

.exchangeContainer {
  width: 90%;
  margin: 0 auto; }

.exchangeHead {
  font-weight: bold;
  padding: 1vw; }

.exchangeContent .exchangeBase {
  font-size: 24px;
  font-weight: bold; }

.exchangeContent ul {
  list-style: none;
  margin: 0;
  padding: 0; }
  .exchangeContent ul li {
    padding-left: 25%;
    border-bottom: 1px solid #bfc4bf;
    display: block;
    margin: 0.5vw auto 0.5vw auto;
    display: grid;
    grid-template-columns: 1fr 1fr; }
    .exchangeContent ul li img {
      width: 10px;
      height: 15px; }


.btn {
  font-family: "Roboto", sans-serif;
  flex: 1vw 1;
  color: #223046;
  border: 1px solid #bfc4bf;
  border-radius: 5px;
  cursor: pointer; }

.btn:hover {
  color: #fff;
  background-color: #223046; }

.sample {
  min-height: 16vw;
  border: 1px solid #fbc4fb; }

.sampleContainer {
  width: 90%;
  margin: 0 auto;
  padding: 1vw 0 1vw 0; }

.sampleHead {
  width: 70%;
  display: grid;
  grid-template-columns: 2fr 1fr; }

.sampleResult {
  margin-top: 2vw;
  margin-bottom: 2vw; }
  .sampleResult ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .sampleResult ul li {
      padding-left: 25%;
      border: 1px solid #bfc4bf;
      display: block;
      margin-bottom: 1vw;
      padding: 0.5vw;
      display: grid;
      grid-template-columns: 2fr 2fr 2fr 0.3fr; }
      .sampleResult ul li img {
        width: 20px;
        height: 15px; }
      .sampleResult ul li button {
        padding: 0.1vw; }

.info {
  min-height: 16vw;
  border: 1px solid #bfc4bf;
  padding: 1vw;
  text-align: center; }
  .info p {
    font-weight: bold;
    font-size: 24px;
    text-transform: uppercase; }

.operation {
  display: grid;
  grid-template-columns: 2fr 1fr 0.5fr;
  grid-column-gap: 1vw; }

.calcResult {
  margin-top: 2vw; }
  .calcResult ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .calcResult ul li {
      padding-left: 25%;
      border-bottom: 1px solid #bfc4bf;
      display: flex;
      justify-content: flex-start; }
      .calcResult ul li img {
        width: 20px;
        height: 15px; }

.calculator {
  min-height: 16vw;
  border: 1px solid #bfc4bf; }

.calculatorContainer {
  width: 90%;
  margin: 0 auto;
  padding: 1vw 0 1vw 0; }

.sidebar {
  border: 1px solid #bfc4cd; }

.sidebarHead {
  border-bottom: 1px solid #bfc4bf;
  display: flex;
  justify-content: center;
  align-items: center; }

.sidebarContent ul {
  list-style: none;
  margin: 0;
  padding: 0; }
  .sidebarContent ul li {
    padding-left: 25%;
    border-bottom: 1px solid #bfbfbf;
    display: block;
    display: flex;
    justify-content: flex-start; }
    .sidebarContent ul li img {
      width: 20px;
      height: 15px; }

.footerAuthor {
  text-align: center;
  font-size: 24px;
  color: rebeccapurple; }

.dark {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #1e1e1e;
  opacity: 0.6;
  z-index: 7;
  display: none; }

.showDark {
  display: block; }

.modalBtn {
  margin: 3vw auto;
  display: flex;
  width: 1vw;
  justify-content: center;
  align-items: center; }

.modalInput {
  margin: 1vw auto;
  padding: 0.5vw;
  display: grid;
  grid-template-columns: 1fr; }
  .modalInput span {
    color: #223046;
    font-family: "Lato", sans-serif;
    font-size: 0.7vw;
    line-height: 1.5vw; }
  .modalInput input {
    height: 1.3vw; }

.modalBtn {
  margin: 3vw auto;
  display: flex;
  width: 1vw;
  justify-content: center;
  align-items: center; }

.modal {
  width: 35.5vw;
  height: 35vw;
  background: #89d6f3;
  border: 1px solid #ccc;
  opacity: 1;
  z-index: 8;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 5px;
  display: none; }
  .modal .error {
    text-align: center;
    color: #f01f30; }

.modalHead {
  padding: 0.7vw;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .modalHead ul {
    margin: 0;
    padding: 0;
    list-style: none; }
    .modalHead ul :nth-child(1) {
      margin-right: 0.5vw; }
    .modalHead ul li {
      display: inline-block; }
      .modalHead ul li a {
        text-decoration: none;
        color: L #223046;
        font-family: "Roboto", sans-serif;
        font-size: 1vw; }
  .modalHead i {
    font-size: 1vw; }

.modalShow {
  display: block; }

.modalInput {
  margin: 1vw auto;
  padding: 0.5vw;
  display: grid;
  grid-template-columns: 1fr; }
  .modalInput label {
    color: #223046;
    font-family: "Lato", sans-serif;
    font-size: 0.7vw;
    line-height: 1.5vw; }
  .modalInput input {
    height: 1.3vw; }
  .modalInput span {
    color: #f01f30;
    font-size: 14px;
    font-weight: bold; }

inputInvalid label {
  color: #f01f30; }

